button {
    position: absolute;
    top: 20px;
    left: 20px;
    cursor: pointer;
    padding: 10px 30px;
    border-radius: 20px;
    animation: animScaleLoop 1s ease-in-out infinite alternate;
    border: 1px solid rgb(59, 59, 59);
}

@keyframes animScaleLoop {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.1);
    }
}
